* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
        Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
}

#search-box {
    font: inherit;
    padding: 8px;
    max-width: 320px;
    margin-bottom: 16px;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

p {
    padding: 0;
    margin: 0;
}

.country-list__item {
    display: flex;
    align-items: center;
}

.country-list__item:not(:last-child) {
    margin-bottom: 20px;
}

.country-list__img {
    display: block;
    width: 40px;
    height: 20px;
    margin-right: 15px;

}

.country-info__wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.country-title {
    margin: 0;
}

.country-info-list__item:not(:last-child) {
    margin-bottom: 10px;
}